.Joke {
    display: flex;
    border-bottom: 2px solid #eee;
    align-items: center;
    justify-content: space-between;
    font-weight: 400;
    padding: 1rem;
}

.Joke-buttons {
    display: flex;
    align-items: center;
    justify-content: center;
    /* width: 15%; */
    margin-right: 1rem;
}

.Joke-text {
    width: 75%;
    font-size: 1.2rem;
}

.Joke-votes {
    width: 50px;
    height: 50px;
    line-height: 50px;
    border-radius: 50%;
    border: 3px solid red;
    text-align: center;
    font-size: 20px;
    font-weight: 300;
    box-shadow: 0px 0px 6px 3px rgb(0 0 255 / 20%);
}

.fa-arrow-up, 
.fa-arrow-down {
    cursor: pointer;
    font-size: 1.2em;
    margin: 10px;
    transition: all 0.3s;
}

.fa-arrow-up:hover {
    animation-name: move-up;
    animation-duration: 0.7s;
    animation-timing-function: ease-out;
    color: rgb(36, 215, 36);
}

.Joke-smiley {
    font-size: 2rem;
    margin-left: auto;
    border-radius: 50%;
    box-shadow: 0px 0px 6px 3px rgb(0 0 255 / 20%);
}

.Joke-smiley:hover {
    animation-name: spin;
    animation-duration: 0.7s;
    animation-timing-function: ease-in-out;
    animation-delay: 0s;
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }
    30% {
        transform: rotate(20deg);
    }
    60% {
        transform: rotate(0deg);
    }
    80% {
        transform: rotate(10deg);
    }
    100% {
        transform: rotate(0deg);
    }
}

@keyframes move-up {
    0%,
    50%,
    100% {
      transform: translateY(0);
    }
    25%,
    75% {
      transform: translateY(-3px);
    }
  }
  
  .fa-arrow-down:hover {
    animation-name: move-down;
    animation-duration: 0.7s;
    animation-timing-function: ease-out;
    color: rgb(252, 29, 29);
  }
  
  @keyframes move-down {
    0%,
    50%,
    100% {
      transform: translateY(0);
    }
    25%,
    75% {
      transform: translateY(3px);
    }
  }